import { Auth, Http } from 'services/services'
import { AuthorizeStep } from './authorize-step'

/**
 * App (Main Module)
 * Mdulo central de la aplicacin
 * @export
 * @class App
 */
export class App {
  /**
   * Mtodo que realiza inyeccin de las dependencias necesarias en el mdulo.
   * Estas dependencias son cargadas bajo el patrn de diseo singleton.
   * @static
   * @returns Array con las dependencias a inyectar: Servicio de autenticacin (Auth),
   * Servicio de conexin http (Http)
   */
  static inject () {
    return [Auth, Http]
  }
  /**
   * Crea una instancia de App.
   * @param {service} authService - Servicio de Autenticacin
   * @param {service} httpService - Servicio de conexin http
   */
  constructor (authService, httpService) {
    this.httpService = httpService
    this.authService = authService
  }
  /**
   * Se encarga del enrutamiento dentro de la aplicacin
   * @param {any} config - Configuracin de la aplicacin
   * @param {any} router - Enrutador principal de la aplicacin
   */
  configureRouter (config, router) {
    config.title = 'UFPS Training Center'
    config.addPipelineStep('authorize', AuthorizeStep)
    config.map([
      {
        route: '',
        redirect: 'problemas'
      },
      // Login
      {
        name: 'login',
        route: 'iniciar-sesion',
        moduleId: './modules/login/login',
        title: 'Iniciar Sesin',
        layoutView: './layouts/not-logged.html',
        settings: {
          roles: ['visitor']
        }
      },
      // Signin
      {
        name: 'signin',
        route: 'registro',
        moduleId: './modules/signin/signin',
        title: 'Regstrate',
        layoutView: './layouts/not-logged.html',
        settings: {
          roles: ['visitor']
        }
      },
      // Recovery Password
      {
        name: 'recovery-password',
        route: 'recuperar-password',
        moduleId: './modules/recovery/recovery-password',
        title: 'Recuperar Contrasea',
        layoutView: './layouts/not-logged.html',
        settings: {
          roles: ['visitor']
        }
      },
      // Reset Password
      {
        name: 'reset-password',
        route: 'cambiar-password/:token',
        moduleId: './modules/recovery/reset-password',
        title: 'Recuperar Contrasea',
        layoutView: './layouts/not-logged.html',
        settings: {
          roles: ['visitor']
        }
      },
      // Problems
      {
        name: 'problems',
        route: 'problemas',
        moduleId: './modules/problems/problem',
        layoutView: './layouts/logged.html',
        nav: true,
        settings: {
          roles: ['admin', 'coach', 'student']
        }
      },
      // Ranking
      {
        name: 'ranking',
        route: 'ranking',
        moduleId: './modules/home/home',
        layoutView: './layouts/logged.html',
        nav: true,
        settings: {
          roles: ['admin', 'coach', 'student']
        }
      },
      // Clases
      {
        name: 'classes',
        route: 'clases',
        moduleId: './modules/home/home',
        layoutView: './layouts/logged.html',
        nav: true,
        settings: {
          roles: ['admin', 'coach', 'student']
        }
      }
    ])
    this.router = router
  }
}
