/**
 * UserLogin
 * Modelo de usuario para el inicio de sesin
 * @export
 * @class UserLogIn
 */
export class UserLogIn {
  /**
   * Crea una instancia de UserLogIn.
   * @param {string} [email=null] - Email del usuario que inicia sesin
   * @param {string} [password=null] - Contrasea del usuario que inicia sesin
   */
  constructor (email = null, password = null) {
    this.email = email
    this.password = password
  }
}
