import { Router } from 'aurelia-router'
import { Auth } from 'services/services'

/**
 * AppHeader (Element)
 * Clase que genera un navbar para los usuarios autenticados.
 * @export
 * @class AppHeader
 */
export class AppHeader {
  /**
   * Mtodo que realiza inyeccin de las dependencias necesarias en el elemento.
   * Estas dependencias son cargadas bajo el patrn de diseo singleton.
   * @static
   * @returns Array con las dependencias a inyectar: Servicio de Autenticacin (Auth),
   * Enrutamiento (Router)
   */
  static inject () {
    return [Auth, Router]
  }

  /**
   * Crea una instancia de AppHeader.
   * @param {service} authService - Servicio de autenticacin
   * @param {service} routerService - Servicio de enrutamiento
   */
  constructor (authService, routerService) {
    this.authService = authService
    this.routerService = routerService
  }

  /**
   * Genera las banderas que indican a los botones del navbar cuando deben activarse.
   * Este mtodo hace parte del ciclo de vida de un componente aurelia y se dispara en el momento
   * en que el componente es aadido al Document Object Model (Dom)
   */
  attached () {
    // Banderas para validar el botn activo en el navbar
    this.problems = this.routerService.navigation.find(i => i.config.name.indexOf('problems') !== -1)
    this.ranking = this.routerService.navigation.find(i => i.config.name.indexOf('ranking') !== -1)
    this.classes = this.routerService.navigation.find(i => i.config.name.indexOf('classes') !== -1)
  }

  /**
   * Cierra sesin en la aplicacin
   */
  logOut () {
    this.authService.logout()
    this.routerService.navigate('iniciar-sesion')
  }
}
